fixed{box-shadow:0px -4px 15px rgba(0,0,0,0.05);margin-left:-16px;margin-right:-16px;overflow:hidden;position:fixed;bottom:0;margin-bottom:0;width:100%;z-index:99999;background-color:#fff}.main .info-modal .tablet-view.fixed .bottom{margin:0 auto;max-width:360px;padding:20px 0px;display:flex;justify-content:space-between;align-items:center}.main .info-modal .tablet-view.fixed .bottom .apply-now{display:flex}.main .info-modal .tablet-view.fixed .bottom .icon{height:22px;width:22px}.main .info-modal .tablet-view.fixed .bottom .icon:first-child{margin-right:20px}.main .info-modal .apply-now{display:none}.main .close{display:none}}@media only screen and (max-width: 410px){.main .info-modal .tablet-view.fixed .bottom{padding-left:16px;padding-right:24px}}::-webkit-scrollbar{width:16px;height:18px}::-webkit-scrollbar-thumb{height:6px;border:4px solid rgba(0,0,0,0);background-clip:padding-box;-webkit-border-radius:10px;background-color:rgba(0,0,0,0.15);-webkit-box-shadow:inset -1px -1px 0px rgba(0,0,0,0.05),inset 1px 1px 0px rgba(0,0,0,0.05)}

